import React, { useState, useEffect, useRef, createContext, useContext } from 'react';
import { BrowserRouter, Routes, Route, Navigate, useNavigate, useLocation, Link } from 'react-router-dom';
import { LineChart, Line, XAxis, YAxis, Tooltip, ResponsiveContainer } from 'recharts';
import { 
  Dumbbell, Activity, Calendar, User, TrendingUp, Plus, 
  Save, LogOut, ChevronRight, X, Trash2, Edit, Download,
  FileSpreadsheet, FileText, Play
} from 'lucide-react';
import './App.css';
import PrivacyPolicyPage from "./pages/PrivacyPolicyPage";

const API_URL = process.env.REACT_APP_BACKEND_URL || 'https://dragon-fit.vercel.app';

// ==============================
// JWT AUTH HELPER (PWA SAFE)
// ==============================
const apiFetch = async (endpoint, options = {}) => {
  const token = localStorage.getItem("token");

  const headers = {
    "Content-Type": "application/json",
    ...(options.headers || {}),
  };

  if (token) {
    headers["Authorization"] = `Bearer ${token}`;
  }

  return fetch(`${API_URL}${endpoint}`, {
    ...options,
    headers,
  });
};


// Auth Context
const AuthContext = createContext(null);

const useAuth = () => useContext(AuthContext);

const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    checkAuth();
  }, []);

  const checkAuth = async () => {
  try {
    const response = await apiFetch("/api/auth/me");

    if (response.ok) {
      const userData = await response.json();
      setUser(userData);
    }
  } catch (error) {
    console.error("Auth check failed:", error);
  } finally {
    setLoading(false);
  }
};


  const login = (userData, token) => {
    setUser(userData);
    if (token) {
      localStorage.setItem('token', token);
    }
  };

  const logout = async () => {
    localStorage.removeItem('token');
    setUser(null);
  };

  return (
    <AuthContext.Provider value={{ user, loading, login, logout, checkAuth }}>
      {children}
    </AuthContext.Provider>
  );
};

// Protected Route
const ProtectedRoute = ({ children }) => {
  const { user, loading } = useAuth();
  const location = useLocation();

  if (loading) {
    return (
      <div className="loading" style={{ minHeight: '100vh' }}>
        <div className="loading-spinner"></div>
      </div>
    );
  }

  if (!user) {
    return <Navigate to="/login" state={{ from: location }} replace />;
  }

  return children;
};

// Auth Callback Component
const AuthCallback = () => {
  const navigate = useNavigate();
  const { login } = useAuth();
  const hasProcessed = useRef(false);

  useEffect(() => {
    if (hasProcessed.current) return;
    hasProcessed.current = true;

    const processAuth = async () => {
      const hash = window.location.hash;
      const sessionIdMatch = hash.match(/session_id=([^&]+)/);
      
      if (sessionIdMatch) {
        const sessionId = sessionIdMatch[1];
        try {
          const response = await apiFetch("/api/auth/session", {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ session_id: sessionId })
          });

          if (response.ok) {
            const data = await response.json();
            login(data.user, data.token);
            navigate('/dashboard', { replace: true });
            return;
          }
        } catch (error) {
          console.error('Auth callback error:', error);
        }
      }
      navigate('/login', { replace: true });
    };

    processAuth();
  }, [navigate, login]);

  return (
    <div className="loading" style={{ minHeight: '100vh' }}>
      <div className="loading-spinner"></div>
    </div>
  );
};

// Login Page
const LoginPage = () => {
  const [isLogin, setIsLogin] = useState(true);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [name, setName] = useState("");
  const [acceptedPrivacy, setAcceptedPrivacy] = useState(false);
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();
  const { login } = useAuth();

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError("");

    //  Validacin extra en registro
    if (!isLogin && !acceptedPrivacy) {
      setError("Debes aceptar la Poltica de Privacidad para registrarte.");
      return;
    }

    setLoading(true);

    try {
      const endpoint = isLogin ? "/api/auth/login" : "/api/auth/register";
      const body = isLogin ? { email, password } : { email, password, name };

      const response = await apiFetch(`${endpoint}`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(body),
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.detail || "Error de autenticacin");
      }

      login(data.user, data.token);
      navigate("/dashboard", { replace: true });
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  const handleGoogleLogin = () => {
    const redirectUrl = window.location.origin + "/dashboard";
    window.location.href = `https://auth.emergentagent.com/?redirect=${encodeURIComponent(
      redirectUrl
    )}`;
  };

  return (
    <div className="auth-page" data-testid="login-page">
      <img src="/dragon-logo.png" alt="DragonFit" className="auth-logo" />
      <h1 className="auth-title neon-text">DragonFit</h1>
      <p className="auth-subtitle">Tu compaero de entrenamiento</p>

      <div className="auth-form">
        <form onSubmit={handleSubmit}>
          {!isLogin && (
            <div className="input-group">
              <label className="input-label">Nombre</label>
              <input
                type="text"
                className="input"
                placeholder="Tu nombre"
                value={name}
                onChange={(e) => setName(e.target.value)}
                required={!isLogin}
                data-testid="register-name-input"
              />
            </div>
          )}

          <div className="input-group">
            <label className="input-label">Email</label>
            <input
              type="email"
              className="input"
              placeholder="tu@email.com"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
              data-testid="email-input"
            />
          </div>

          <div className="input-group">
            <label className="input-label">Contrasea</label>
            <input
              type="password"
              className="input"
              placeholder=""
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
              data-testid="password-input"
            />
          </div>

          {/*  Checkbox solo en registro */}
          {!isLogin && (
            <div
              style={{
                display: "flex",
                alignItems: "flex-start",
                gap: "8px",
                marginBottom: "16px",
                fontSize: "13px",
              }}
            >
              <input
                type="checkbox"
                checked={acceptedPrivacy}
                onChange={(e) => setAcceptedPrivacy(e.target.checked)}
                style={{ marginTop: "3px" }}
                data-testid="privacy-checkbox"
              />
              <span style={{ color: "var(--muted-foreground)" }}>
                He ledo y acepto la{" "}
                <Link
                  to="/privacy"
                  target="_blank"
                  style={{ fontWeight: 600 }}
                >
                  Poltica de Privacidad
                </Link>
              </span>
            </div>
          )}

          {error && (
            <p
              style={{
                color: "var(--destructive)",
                fontSize: "14px",
                marginBottom: "16px",
              }}
            >
              {error}
            </p>
          )}

          <button
            type="submit"
            className="btn btn-primary w-full"
            disabled={loading}
            data-testid="submit-button"
          >
            {loading
              ? "Cargando..."
              : isLogin
              ? "Iniciar Sesin"
              : "Registrarse"}
          </button>
        </form>

        <div className="auth-divider">o</div>

        <button
          className="btn btn-secondary w-full"
          onClick={handleGoogleLogin}
          data-testid="google-login-button"
        >
          Continuar con Google
        </button>

        <p className="auth-switch">
          {isLogin ? "No tienes cuenta? " : "Ya tienes cuenta? "}
          <a
            href="#"
            onClick={(e) => {
              e.preventDefault();
              setIsLogin(!isLogin);
            }}
          >
            {isLogin ? "Regstrate" : "Inicia Sesin"}
          </a>
        </p>
      </div>
    </div>
  );
};

// Dashboard Page
