// useParams hook for workout detail
const useParams = () => {
  const location = useLocation();
  const path = location.pathname;

  // Buscar workoutId
  const workoutMatch = path.match(/\/workout\/([^/]+)/);
  if (workoutMatch) {
    return { workoutId: workoutMatch[1] };
  }

  // Buscar sessionId
  const sessionMatch = path.match(/\/session\/([^/]+)/);
  if (sessionMatch) {
    return { sessionId: sessionMatch[1] };
  }

  // Ninguno encontrado
  return {};
};


// Main App Router
const AppRouter = () => {
  const location = useLocation();

  // Check for session_id in URL hash (OAuth callback)
  if (location.hash?.includes('session_id=')) {
    return <AuthCallback />;
  }

  return (
    <Routes>
      <Route path="/login" element={<LoginPage />} />
      <Route path="/privacy" element={<PrivacyPolicyPage />} />
      <Route path="/dashboard" element={
        <ProtectedRoute>
          <DashboardPage />
          <BottomNav />
        </ProtectedRoute>
      } />
      <Route path="/progress" element={
        <ProtectedRoute>
          <ProgressPage />
          <BottomNav />
        </ProtectedRoute>
      } />
      <Route path="/calendar" element={
        <ProtectedRoute>
          <CalendarPage />
          <BottomNav />
        </ProtectedRoute>
      } />
      <Route path="/profile" element={
        <ProtectedRoute>
          <ProfilePage />
          <BottomNav />
        </ProtectedRoute>
      } />
      <Route path="/session/:sessionId" element={
        <ProtectedRoute>
          <SessionDetailPage />
          <BottomNav />
        </ProtectedRoute>
      } />
      <Route path="/workout/create" element={
        <ProtectedRoute>
          <CreateWorkoutPage />
          <BottomNav />
        </ProtectedRoute>
      } />
       <Route path="/workout/:workoutId/log/:dayIndexLog" element={
        <ProtectedRoute>
          <LogSessionPage />
          <BottomNav />
        </ProtectedRoute>
      } />
        <Route path="/workout/:workoutId/edit" element={
          <ProtectedRoute>
            <EditWorkoutPage />
            <BottomNav />
          </ProtectedRoute>
        }/>
      <Route path="/workout/:workoutId" element={
        <ProtectedRoute>
          <WorkoutDetailPage />
          <BottomNav />
        </ProtectedRoute>
      } />
      <Route path="*" element={<Navigate to="/dashboard" replace />} />
    </Routes>
  );
};

// Main App
function App() {
  return (
    <BrowserRouter>
      <AuthProvider>
        <div className="app-container">
          <AppRouter />
        </div>
      </AuthProvider>
    </BrowserRouter>
  );
}

export default App;